

.all {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    background-color: #888888;
    .top {
        background-color: #888888;
        width: 100vw;
        height: 20vw;
    }
    .middle {
        flex: 1;
        width: 100vw;
        height: 0;
    }
    .bottom {
        background-color: #888888;
        width: 100vw;
        height: 20vw;
    }
}
